body {
	margin: 0;
	background-color:#1e93d2ab;
	transition: opacity 0.5s ease-in-out;
  }

  #initial-view {
   position:absolute;
   font-family: "Lora", serif; 
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	height: 100%;
	width: 100%;
	background-color:#1e93d2ab;
	background-size: cover;
	transition: opacity 0.5s ease-in-out;
  }

  #initial-view.hidden{
	  opacity: 0;
	  pointer-events: none;
  }

  header h1 {
	font-size: 3em;
	color: rgb(255, 255, 255);
	padding-bottom: 18px;
	pointer-events: none;
  }

  main p {
	font-size: 2em;
	color: rgb(255, 255, 255);
	padding-bottom: 50px;
	pointer-events: none;
  }

  footer {
	padding-top: 270px;
	font-size: 1.3em;
	font-weight: bold;
  }

  .link {
	padding: 10px 20px;
	font-family: "Lora", serif; 
	align-items: center;
	display: inline-flex;
	color: rgb(255, 255, 255);
	text-decoration: none;
	background-color: transparent;
	border-radius: 40px;
	border: 2px solid rgb(255, 255, 255);
	transition: background-color 0.3s ease;
  }

  .link:hover {
	background-color: #97dcff;
	transition-delay: 0.01s;
  }

  #navbar {
	opacity: 0.2;
	transform: translateY(-75px);
	transition: opacity 1s ease-in-out, transform 1s ease-in-out;
  }

  #navbar.visible {
	opacity: 1;
	transform: translateY(0);
	transition-delay: 0.5s;
  }

  .hidden {
	display: none;
  }

  .fade-out {
	opacity: 0;
  }

  .fade-in {
	opacity: 1;
  }


::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: #888; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background: #555; 
}

.progress-bar-container {
    position: absolute;
	font-family: "Lora", sans-serif; 
	font-weight: bold;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background-color: #1e93d2ab;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#progress-bar {
    width: 30%;
    margin-top: 0.5%;
    height: 2%;
}

label {
    color: white;
    font-size: 2rem;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
html, body, footer{
	position: absolute;
	cursor: none;
	margin: 0;
}


.custom-cursor {
	position: fixed;
	top: 0;
	left: 0;
	display: none;
 	 pointer-events: none; 
 	 z-index: 1000;
  
}

.site-wide {
	width: 30px;
	height: 30px;
	border-radius: 50%;
	border: 2px solid black;
	z-index: 1000;
	pointer-events: none;
  
}

.site-wide .pointer {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 5px;
	height: 5px;
	border-radius: 50%;
	background-color: black;
	transition: width 0.1s ease-in-out, height 0.1s ease-in-out;
	z-index: 1000;
	pointer-events: none;

}

.site-wide.active .pointer {
	width: 20px;
	height: 20px;
	pointer-events: none;
	z-index: 1000;
}

#creditawal{
	position: absolute;
	top: 720px;
	font-size: 14px;
	font-family: "Lora", sans-serif; 
	font-weight: bold;
