	border-style: solid;
	border-radius: 0px;
	padding-left: 688.5px;
	padding-top: 5px;
	padding-bottom: 5px;
	padding-right: 688.5px;
	background-color: rgba(140, 215, 255, 0.15);
	border-color: rgba(0,0,0,0);
	opacity: 0;
	transform: translateY(75px);
	transition: opacity 1s ease-in-out, transform 1s ease-in-out;
}
#credit.visible{
	opacity: 1;
